import global from './../../global'
const AnimateState = {
    invalid: -1,
    default: 1,
    idle: 2,
    walk: 3,
    run: 4,
    jump: 5,
    attack: 6
};
cc.Class({
    extends: cc.Component,
    editor: {
        requireComponent: dragonBones.ArmatureDisplay
    },

    properties: {

        defaultAnimate: "",
        idleAnimate: "",
        walkAnimate: "",
        jumpAnimate: "",
        attackAnimate: ""

    },
    onLoad: function () {
        this._armatureDisplay = this.getComponent(dragonBones.ArmatureDisplay);
        this._armature = this._armatureDisplay.armature();
        // this._armatureDisplay.addEventListener(dragonBones.EventObject.FADE_IN_COMPLETE, this.animateCompleteListener, this);
        this._armatureDisplay.addEventListener(dragonBones.EventObject.FADE_IN_COMPLETE,this.animateCompleteListener, this);
        dragonBones.WorldClock.clock.add(this._armature);
        this.state = AnimateState.invalid;
        this.node.parent.on('jump', (event)=>{
            console.log('');
            this.setState(AnimateState.jump);
        });

        this.node.parent.on('jump-end', ()=>{
           console.log('' + this.oldState);
           if (this.oldState === AnimateState.walk || this.oldState === AnimateState.default){
               this.setState(AnimateState.walk);
           }
        });

        this.node.parent.on('walk', (event)=>{
            console.log('');
            this.setState(AnimateState.walk);
        });

        this.node.parent.on('idle', (event)=>{
            console.log('idle');
            this.animateSpeed = event.detail;
            this.setState(AnimateState.idle);
        });
        this.node.parent.on('attack', (event)=>{
            console.log('');
            this.setState(AnimateState.attack);
        });

        // this.node.parent.on('time-scale', ()=>{
        //    //
        //     console.log(' ' + this._armatureDisplay.timeScale);
        //     this._armatureDisplay.timeScale = 0;
        // });

        global.account.gameCtl.onRun(()=>{
            this.setState(AnimateState.default);
        });
        this.setState(AnimateState.default);
    },
    animateFrameListener(){

    },
    animateCompleteListener(event){
        console.log('');
        console.log('event = ' + event.type);
        // console.log('' + this.state);
        // console.log('' + this.oldState);
        // if (this.state === AnimateState.jump && this.oldState === AnimateState.walk){
        //     console.log('');
        //     this.setState(AnimateState.walk);
        // }

    },
    idle: function () {
    },
    walk: function () {
        this.setState(AnimateState.walk);
    },
    jump: function () {
    },
    attack: function () {
    },
    playAnimate: function (state) {

    },
    setState(state){
        if (this.state === state){
            return;
        }
        this.oldState = this.state;
        switch (state){
            case AnimateState.default:
                this._armature.animation.fadeIn(this.defaultAnimate, 0.2,-1,0);
                break;
            case AnimateState.jump:

                this._armature.animation.fadeIn(this.jumpAnimate, 0,1,0);
                // setTimeout(()=>{
                //     this.setState(AnimateState.walk);
                // }, 300);
                break;
            case AnimateState.walk:

                console.log('dragon bone walk');
                this._armature.animation.fadeIn(this.walkAnimate, 0.2,-1,0);


                break;
            case AnimateState.run:
                break;
            case AnimateState.idle:
                console.log('idle ');
                this._armature.animation.fadeIn(this.idleAnimate, 0.2, -1, 0);

                break;
            case AnimateState.attack:
                this._armature.animation.fadeIn(this.attackAnimate, 0, 1, 0);

                break;
            default:
                break;
        }

        this.state = state;
    },
    update(dt){
        this._armatureDisplay.timeScale = defines.timeScale;
    }

});
