
cc.Class({
    extends: cc.Component,

    properties: {

    },
    onLoad(){
        this.node.on('fly', ()=>{
            console.log('');
            let action = cc.bezierTo(0.4, [this.node.position,cc.p(this.node.position.x, this.node.position.y + 200), cc.p(-717,377)]);
            this.node.runAction(cc.sequence(action, cc.callFunc(()=>{
                this.node.dead = true;
            })));
            this.node.runAction(cc.scaleTo(0.4, 0.2,0.2));
        });
    },
    onDestroy(){
        console.log('');
    }
});
