import global from './../../global'
cc.Class({
    extends: cc.Component,

    properties: {
        timeCutDownLabel: {
            default: null,
            type: cc.Label
        },
        resultLabel: {
            default: null,
            type: cc.Label
        }
    },

    onLoad(){
        global.account.gameCtl.onTipsBegan((data)=> {
            console.log('' + JSON.stringify(data));
            this.node.active = true;
            this.timeCutDown = 4;
        });

        global.account.gameCtl.onTipsEnd(()=>{
            this.node.active = false;
        });


    },

    start () {
        this.node.active = false;

    },
    update(dt){
        if (this.timeCutDown > 0){
            this.timeCutDown -= dt;
            if (this.timeCutDown <= 0){
                this.timeCutDown = 0;
                this.resultLabel.string = '!';
                setTimeout(()=>{
                    this.resultLabel.string = '';
                }, 1000);
                global.account.gameCtl.tipsEnd({
                    result: 'success'
                })
            }
            this.timeCutDownLabel.string = Math.floor(this.timeCutDown) + '';

        }
    }
});
