import global from './../../global'
cc.Class({
    extends: cc.Component,

    properties: {
        stubSpriteFrameList: {
            default: [],
            type: cc.SpriteFrame
        }
    },
    initWithData: function (data) {
        this.speedX = data.speed;
    },
    onLoad(){
        if (this.stubSpriteFrameList.length !== 0){
            this.addComponent(cc.Sprite).spriteFrame = this.stubSpriteFrameList[0];
            let collider = this.node.addComponent(cc.BoxCollider);
            collider.size = cc.size(this.node.width, this.node.height);
        }
        this.node.on('fly', ()=>{
            this.fly = true;
            console.log('');
        });


    },
    update(dt){
        if (global.account.gameCtl.isRunning()){
            if (this.speedX && this.fly === undefined){
                this.node.position = cc.p(this.node.position.x - this.speedX * dt * defines.timeScale, this.node.position.y);
            }
        }

        if ((this.node.position.x + this.node.width * 0.5) < cc.winSize.width * -0.5){
            this.node.dead = true;
        }

    },
    onDestroy(){
        console.log('');
    }
});
