
cc.Class({
    extends: cc.Component,

    properties: {

        gameNode: {
            default: null,
            type: cc.Node
        },
        stubPrefab: {
            default: null,
            type: cc.Prefab
        },
        prizePrefab: {
            default: null,
            type: cc.Prefab
        },
        enemyPrefabList: {
            default: [],
            type: cc.Prefab
        },
        stubSpeedX: 0,
        initPosNode: {
            default: null,
            type: cc.Node
        },
        initPrizePosNode: {
            default: null,
            type: cc.Node
        }
    },
    onLoad(){
        this.stubList = [];

        this.addStubIndex = 0;

        this.prizeList = [];

        this.enemyList = [];


        this.coinList = [];

        this.addStub();


        //
        this.addCoinFlag = 0;


        this.addCoinFlagDurection = 4;


        this.addCoinDurection = 0;

    },
    update(dt){
        // for (let i = 0 ; i < this.stubList.length ; i ++){
        //     let stub = this.stubList[i];
        //     stub.position = cc.p(stub.position.x - this.stubSpeedX * dt * defines.timeScale,stub.position.y);
        // }
        // for (let i = 0 ; i < this.stubList.length ; i ++){
        //     let stub = this.stubList[i];
        //     if (stub.position.x + stub.width * 0.5 < cc.winSize.width * -0.5){
        //         this.stubList.splice(i, 1);
        //         stub.destroy();
        //     }
        //     // if (stub.isFly){
        //     //     this.stubList.splice(i, 1);
        //     // }
        // }


        for (let i = 0 ; i < this.stubList.length ; i ++){
            let enemy = this.stubList[i];
            if (enemy.dead === true){
                this.stubList.splice(i, 1);
                enemy.destroy();
            }
            // if (this.stubList[i].dead === true){
            //     this.stubList.splice(i, 1);
            //
            // }
        }


        for (let i = 0 ;i < this.coinList.length ; i ++){
            let coin = this.coinList[i];
            if (coin.dead){
                this.coinList.splice(i, 1);
                coin.destroy();
            }
        }


        if (this.stubList.length !== 0 && this.stubList[this.stubList.length - 1].position.x < cc.winSize.width * 0.2){
            if (this.addStubIndex === 3){
                this.addStubIndex = 0;
                this.addEnemy();
            }else {
                this.addStub();
                // if (Math.floor(Math.random() * 3) === 0){
                //     // this.addPrize();
                // }
            }
        }


        this.addCoinFlagDurection -= dt;
        if (this.addCoinFlagDurection <=0 && this.addCoinFlag === 0){
            this.addCoinFlagDurection = 4;
            this.addCoinFlag = Math.floor(Math.random() * 3 + 2);
        }
        if (this.addCoinFlag > 0){
            if (this.coinList.length === 0 ){
                this.addCoin();
            }else {
                if ((this.initPrizePosNode.position.x - this.coinList[this.coinList.length - 1].position.x ) > 300){
                    this.addCoin();
                }
            }
        }




        //
        // console.log('stub = ' + this.stubList.length);
    },
    addCoin(){
        this.addCoinFlag --;
        let coin = this.createStub(this.initPrizePosNode, this.prizePrefab);
        coin.zIndex = 2;
        this.coinList.push(coin);
    },
    addStub(){
        // let stub = cc.instantiate(this.stubPrefab);
        // stub.parent = this.gameNode;
        // stub.position = this.initPosNode.position;
        let stub = this.createStub(this.initPosNode, this.stubPrefab);
        this.stubList.push(stub);
        this.addStubIndex ++;
    },
    addPrize(){
        let stub = this.createStub(this.initPrizePosNode, this.prizePrefab);
        stub.zIndex = 2;
        this.prizeList.push(stub);
    },
    addEnemy(){
        let enemy = this.createStub(this.initPosNode, this.enemyPrefabList[0]);
        enemy.emit('init-manager', this);
        // this.enemyList.push(enemy);
        // console.log('add enemy');
        this.stubList.push(enemy);
    },
    createStub(initPos, prefab){
        let stub = cc.instantiate(prefab);
        stub.zIndex = 0;
        stub.parent = this.gameNode;
        stub.position = initPos.position;
        if (stub.getComponent('stub')){
            stub.getComponent('stub').initWithData({speed: this.stubSpeedX});
        }
        return stub
    }

});
