cc.Class({
    extends: cc.Component,

    properties: {
       progress: 0
    },
    onLoad(){

        this.currentWidth = this.node.width;

    },
    update(){
        this.node.width = this.currentWidth * this.progress;
    },
    setProgress(progress){
        // console.log('progress = ' + progress);
        this.progress = progress;
    }
}
);
