
import global from './../global'
cc.Class({
    extends: cc.Component,

    properties: {
        playerNode: {
            default: null,
            type: cc.Node
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        // let collisionManager = new cc.director.getCollisionManager();
        // collisionManager.enabled = true;
        // collisionManager.enabledDrawBoundingBox = true;
        // collisionManager.enabledDebugDraw = true;
        // console.log('0');
        cc.director.getCollisionManager().enabled = true;
        // cc.director.getCollisionManager().enabledDebugDraw = true;
        console.log('enter animate');
        this.playerNode.emit('walk');
        let action = cc.moveTo(1, cc.p(this.playerNode.position.x + 400, this.playerNode.position.y));
        this.playerNode.runAction(cc.sequence(action, cc.callFunc(()=>{console.log('');
            global.account.gameCtl.run();
        })));
    },

    start () {


    },
    update(dt){
        global.account.gameCtl.update(dt);
    }
});
